        '— Давным-давно, — начал музыкант, — жил я в этих лесах, свободный и беззаботный. Но однажды, в ночь полнолуния, я спас заблудившегося духа леса. В благодарность он подарил мне эту маску и силу, чтобы защищать природу и напоминать людям о важности гармонии с ней.',
